import { createApp } from 'vue'
import App from './App.vue'
import mainStore from './library/stores/mainStore'
import AJAX from './library/plugins/AJAX'
import router from './library/routers/dashboard/router'

export const mainStoreObject = mainStore

const app = createApp(App)
app.use(mainStore)
  .use(AJAX)
  .use(router)
  .mount('#app')

app.config.globalProperties.$filters = {
  localDate(x) {
    return new Date(x).toLocaleString('fa')
  }
}
