export default [
  {
    path: '/',
    redirect : '/website/settings'
  },
  {
    path: '/member/profile',
    component: () => import('../../../components/user/Profile'),
    meta: { title: 'پروفایل کاربر' }
  },

  {
    path: '/qna',
    component: () => import('../../../components/qna/Questions'),
    meta: { title: 'پرسش‌ها و پاسخ‌ها' }
  },
  {
    path: '/qna/new',
    component: () => import('../../../components/qna/QuestionForm'),
    meta: { title: 'ثبت پرسش جدید' }
  },
  {
    path: '/qna/:qnAId/edit',
    component: () => import('../../../components/qna/QuestionForm'),
    meta: { title: 'ویرایش پرسش' },
    props: true
  },


  //website
  {
    path: '/website/settings',
    component: () => import('../../../components/website/WebsiteSettings'),
    meta: { title: 'تنظیمات وبسایت' },
    props: true
  },

  {
    path: '/contact-us',
    component: () => import('../../../components/contact-us/ContactUs'),
    meta: { title: 'سوالات متداول' }
  }
]
