import { createStore } from 'vuex'
import notifications from './notifications'

const USER_STORAGE_KEY = 'admin-user-storage'

export default createStore({
  state: () => ({
    user: undefined
  }),
  mutations: {
    setUser: (state, value) => {
      state.user = value
    }
  },
  actions: {
    saveUser(context, obj) {
      if (!obj.user) {
        return
      }

      context.commit('setUser', obj.user)
      if (obj.remember) {
        localStorage.setItem(USER_STORAGE_KEY, JSON.stringify(obj.user))
      } else {
        sessionStorage.setItem(USER_STORAGE_KEY, JSON.stringify(obj.user))
      }
    },
    retrieveUser(context) {
      const user = localStorage.getItem(USER_STORAGE_KEY) || sessionStorage.getItem(USER_STORAGE_KEY)
      context.commit('setUser', user ? JSON.parse(user) : null)
    },
    removeUser(context) {
      localStorage.removeItem(USER_STORAGE_KEY)
      sessionStorage.removeItem(USER_STORAGE_KEY)
      context.commit('setUser', null)
    }
  },
  modules: {
    notifications
  }
})
