<template>
  <div>
    <div class="row m-0 bg-white">

      <div class="col-sm-4 d-flex flex-column" id="side-panel">

        <div class="text-center p-3">
          <h1>داشبورد مدیریت</h1>
          <p>از این قسمت شما میتوانید وارد ناحیه مدیریت شوید.</p>
        </div>

        <form @submit.prevent="onFormSubmit" class="m-auto p-3" style="width: 350px">

          <div>
            <input type="text" class="form-control text-center"
                   required="required"
                   placeholder="نام کاربری"
                   v-model="model.username"
            />
          </div>

          <div class="mt-2">
            <input :type="showPassword ? 'text': 'password'"
                   v-model="model.password"
                   required="required"
                   class="form-control text-center" placeholder="کلمه عبور"
            />
          </div>


          <div class="mt-2 d-flex align-items-center flex-row-reverse">

            <div class="ms-2 form-check">
              <input type="checkbox" class="form-check-input" id="form-check-remember-me" v-model="model.remember"/>
              <label for="form-check-remember-me" class="form-check-label">
                مرا به خاطر بسپار
              </label>
            </div>

            <button class="btn btn-primary flex-fill" id="login-button" v-if="!working">
              <span class="fa fa-sign-in-alt"></span>
              <span class="ms-2">ورود کاربر</span>
            </button>

            <button class="btn btn-primary flex-fill" v-else>
              <span class="fa fa-spin fa-sync"></span>
              <span class="ms-2">ورود کاربر</span>
            </button>

          </div>

          <ul class="mt-2 text-danger">
            <li v-for="(x,k) in errors" :key="k">
              {{ x }}
            </li>
          </ul>

          <div class="mt-2">
            <a href="/">
              فراموشی کلمه عبور
            </a>
          </div>
        </form>

        <div class="mt-auto small text-center">
          گروه نرم افزاری ارغنون
        </div>
        <code class="text-center small">
          Dashboard V1.00 Beta
        </code>
      </div>

      <div class="col-sm position-relative text-center" id="content-box">
        <img src="../assets/images/website-builder.svg"/>

        <div class="text-center" style="position: absolute; left: 0; right: 0; bottom: 0;">
          <code class="text-center small text-info">
            All Right Reserved &copy; BH
          </code>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data: () => ({
    showPassword: false,
    working: false,
    model: {
      remember: false,
      username: '',
      password: ''
    },
    errors: {}
  }),
  methods: {
    onFormSubmit() {
      if (this.working) {
        return
      }
      this.working = true
      this.errors = {}

      this.$fetch('/users/authenticate', 'post', {
        userName: this.model.username,
        passCode: this.model.password
      })
          .then(x => {
            this.$store.dispatch('saveUser', {
              user: x.data,
              remember: this.model.remember
            })
          })
          .catch(() => {
            this.errors = {
              'username': 'نام کاربری و یا کلمه عبور صحیح نیست'
            }
          })
          .finally(() => this.working = false)
    }
  }
}
</script>

<style scoped lang="scss">
#login-button {
  background: #00ffff;
  border-color: #00ffff;
}

input:focus,
button:focus {
  box-shadow: 0 0 2px 0 #00000099;
  outline: none !important;
}

#side-panel {
  background: #eff1f6;
  min-height: 50vh;
}

@media (min-width: 576px) {
  #content-box {
    height: 100vh;
  }
}
</style>
