      <span class="fa fa-sign-out-alt"></span>
    </button>
  </div>
</template>

<script>
export default {
  name: 'ContentTopBar',
  methods: {
    onLogOut() {
      this.$store.dispatch('removeUser')
    }
  }
}
</script>

