import axios from 'axios'

export const AO = axios.create({
  baseURL: process.env.NODE_ENV === 'production' ? '/api' : 'https://localhost:44397/api'
})
export default {
  install(app) {
    app.mixin({
      methods: {
        $fetch(url, method = 'get', data, onUploadProgress = undefined) {
          if (method.toLowerCase() === 'get' && typeof data === 'object') {
            let urlEncoded = ''
            for (const k in data) {
              urlEncoded += encodeURIComponent(k) + '=' + encodeURIComponent(data[k]) + '&'
            }
            url += '?' + urlEncoded.substr(0, urlEncoded.length - 1)
          }
          return AO(url, {
            method,
            data,
            onUploadProgress
          })
        }
      }
    })
  }
}
