import { mainStoreObject } from '../../main'

export default {
  namespaced: true,

  state: () => ({
    notifications: {}
  }),
  mutations: {
    addNotification(s, notification) {
      const key = Date.now() + '-' + Math.random()
      s.notifications[key] = notification
      setTimeout(() => {
        if (s.notifications[key]) {
          delete s.notifications[key]
        }
      }, 5000)
    },
    removeNotification(s, index) {
      delete s.notifications[index]
    }
  },
  getters: {
    getNotifications(state) {
      return state.notifications
    }
  }
}

export function dispatchNotification(body, title = '', style = {}) {
  mainStoreObject.commit('notifications/addNotification', {
    body,
    title,
    style
  })
}

export function removeNotificationByIndex(index) {
  mainStoreObject.commit('notifications/removeNotification', index)
}
