<template>
  <div id="notification-container">
    <div class="item" v-for="(x,k) in $store.getters['notifications/getNotifications']" :key="k">
      <div class="fw-bolder d-flex align-items-center p-2">
        <div>{{ x.title }}</div>

        <button class="btn btn-danger btn-sm ms-auto" @click="removeNotificationByIndex(k)">
          <span class="fa fa-times"></span>
        </button>
      </div>
      <div v-html="x.body" class="p-2 text-justify"></div>
    </div>
  </div>
</template>

<script>
import { removeNotificationByIndex } from '../library/stores/notifications'

export default {
  name: 'NotificationsContainer',
  methods: {
    removeNotificationByIndex
  }
}
</script>

