<template>
  <component :is="component">
  </component>
</template>

<script>
import './assets/stylesheet/main.scss'
import './assets/stylesheet/fonts/fonts.scss'
import Login from './components/Login'
import Dashboard from './components/Dashboard'
import { h } from 'vue'

export default {
  name: 'App',
  components: {
    Login,
    Dashboard
  },
  computed: {
    component() {
      if (this.$store.state.user === undefined) {
        return { render: () => h('div', 'Loading, Please wait') }
      }
      if (this.$store.state.user) {
        return Dashboard
      } else {
        return Login
      }
    }
  },
  mounted() {
    this.$store.dispatch('retrieveUser')
  }
}
</script>

